body {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
  font-size: 16px;
  background-color: #ebebeb;
  color: #222222;
  touch-action: pan-x pan-y;
}

.App {
  text-align: center;
}

/*============================================================================*\
    Page Layout
\*============================================================================*/
.row-white {
  background-color: white;
}

@media (max-width: 767px) {
  .container-fluid {
    padding: 0px;
  }
  .pl-sm-0 {
    padding-right: 0;
  }
}

/*============================================================================*\
    Bootstrap styling
\*============================================================================*/
.is-valid.form-control {
  border-color: #e5e5e5;
}
.form-control {
  border-radius: 0.5rem;
}
.btn {
  border-radius: 0.4rem;
}
.btn-primary {
  background-color: #99230e;
  border-color: #99230e;
}
.btn-primary:hover {
  background-color: #b22810;
  border-color: #b22810;
}
.btn-success {
  background-color: #54984a;
  border-color: #54984a;
}
.btn-success:hover {
  background-color: #5ca651;
  border-color: #5ca651;
}
.btn-warning {
  background-color: #d9a700;
  border-color: #d9a700;
  color: white;
}
.btn-warning:hover {
  background-color: #e8b200;
  border-color: #d9a700;
  color: white;
}
.btn-info {
  background-color: #e5e5e5;
  border-color: #e5e5e5;
  color: #636363;
}
.btn-info:hover {
  background-color: #ededed;
  border-color: #999;
  color: #636363;
}
.btn.disabled {
  background-color: #d4d4d4;
  opacity: 1;
  border: 0;
}
.btn.disabled:hover {
  background-color: #d4d4d4;
}

.modal__container .modal-content {
  border-radius: 0.6rem;
}
.modal__header {
  background-color: #2e2e2e;
  color: white;
  font-size: 1.3rem;
  text-align: center;
  border-radius: 0.5rem 0.5rem 0 0;
}
.modal__header button {
  color: white;
}
.modal__header button:hover {
  color: white;
}
.modal__body {
  text-align: center;
  padding: 0;
}
.modal__body__content {
  padding: 1.5rem;
}
.modal__body__food__name {
  font-size: 1.4rem;
  font-weight: bold;
  text-align: left;
}
.modal__body__food_description {
  display: none;
  text-align: left;
}

.modal__body__button__line {
  margin: 0;
  position: -webkit-sticky;
  position: sticky;
  bottom: 0;
}
.modal__body__button__strip {
  background-color: #d4d4d4;
  position: relative;
  top: 3.3rem;
  z-index: 0;
  height: 1.6rem;
  width: 100%;
}
.modal__body__button {
  margin: 1rem auto;
  padding: 0.8rem 1.6rem;
  z-index: 1;
  position: relative;
  text-transform: uppercase;
}

.modal-open .modal {
  overflow-y: scroll; /* has to be scroll, not auto */
  -webkit-overflow-scrolling: touch;
}

@media (min-width: 900px) {
  .modal__container {
    max-width: 970px;
    margin: 2rem auto;
  }
}

